import { FC } from 'react';
import { clsx } from 'clsx';

import { GridProps, GridItemProps, GridScope } from './grid.interface';

const extractClasses = (classes: { [key: string]: string }, iterable: {}) =>
  Object.entries(iterable).map(([breakpoint, column]) => classes[`${breakpoint}-${column}`]);

export const Grid: FC<GridProps> = ({
  columns = { xs: 4, sm: 1, md: 2, lg: 3, xl: 4, '2xl': 4 },
  children,
}: GridProps) => {
  const gridClasses = {
    'xs-1': 'zep-xs:grid-cols-1',
    'xs-2': 'zep-xs:grid-cols-2',
    'xs-3': 'zep-xs:grid-cols-3',
    'xs-4': 'zep-xs:grid-cols-4',
    'sm-1': 'zep-sm:grid-cols-1',
    'sm-2': 'zep-sm:grid-cols-2',
    'sm-3': 'zep-sm:grid-cols-3',
    'sm-4': 'zep-sm:grid-cols-4',
    'md-1': 'zep-md:grid-cols-1',
    'md-2': 'zep-md:grid-cols-2',
    'md-3': 'zep-md:grid-cols-3',
    'md-4': 'zep-md:grid-cols-4',
    'lg-1': 'zep-lg:grid-cols-1',
    'lg-2': 'zep-lg:grid-cols-2',
    'lg-3': 'zep-lg:grid-cols-3',
    'lg-4': 'zep-lg:grid-cols-4',
    'xl-1': 'zep-xl:grid-cols-1',
    'xl-2': 'zep-xl:grid-cols-2',
    'xl-3': 'zep-xl:grid-cols-3',
    'xl-4': 'zep-xl:grid-cols-4',
    '2xl-1': 'zep-2xl:grid-cols-1',
    '2xl-2': 'zep-2xl:grid-cols-2',
    '2xl-3': 'zep-2xl:grid-cols-3',
    '2xl-4': 'zep-2xl:grid-cols-4',
  };

  return (
    <div
      className={clsx(GridScope, 'grid', 'gap-zep-1', 'content-stretch', extractClasses(gridClasses, columns))}
      data-testid="zep-grid"
    >
      {children}
    </div>
  );
};

export const GridItem: FC<GridItemProps> = ({ children, colSpan = { xs: 2 } }) => {
  const itemClasses = {
    'xs-1': 'zep-xs:col-span-1',
    'xs-2': 'zep-xs:col-span-2',
    'xs-3': 'zep-xs:col-span-3',
    'xs-4': 'zep-xs:col-span-4',
    'sm-1': 'zep-sm:col-span-1',
    'sm-2': 'zep-sm:col-span-2',
    'sm-3': 'zep-sm:col-span-3',
    'sm-4': 'zep-sm:col-span-4',
    'md-1': 'zep-md:col-span-1',
    'md-2': 'zep-md:col-span-2',
    'md-3': 'zep-md:col-span-3',
    'md-4': 'zep-md:col-span-4',
    'lg-1': 'zep-lg:col-span-1',
    'lg-2': 'zep-lg:col-span-2',
    'lg-3': 'zep-lg:col-span-3',
    'lg-4': 'zep-lg:col-span-4',
    'xl-1': 'zep-xl:col-span-1',
    'xl-2': 'zep-xl:col-span-2',
    'xl-3': 'zep-xl:col-span-3',
    'xl-4': 'zep-xl:col-span-4',
    '2xl-1': 'zep-2xl:col-span-1',
    '2xl-2': 'zep-2xl:col-span-2',
    '2xl-3': 'zep-2xl:col-span-3',
    '2xl-4': 'zep-2xl:col-span-4',
  };

  return <div className={`${clsx(extractClasses(itemClasses, colSpan))}`}>{children}</div>;
};
